html, body {
  display: inline-flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  background: linear-gradient(to bottom, #20B2AA, #32CD32);
  justify-content: center;
  align-items: center;
}

h1 {
  text-align: center;
}

.LogInButton {
  justify-content: right;
}

input {
  justify-content: space-around;
  border-radius: 25px;
  border: 2px solid #609;
  padding: 20px;
  width: 200px;
  height: 15px;
  margin: 5px;
}
